<template>
  <div class="pos">
    <el-row>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs class="tri-navigator">
          <el-tab-pane label="点餐" name="orderIt">
            <el-table :data="tableData" border style="width:100%">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" label="数量" width="50"></el-table-column>
              <el-table-column prop="price" label="价格" width="70"></el-table-column>
              <el-table-column label="action" width="100" fixed="right">
                <template scope="orderScope">
                  <el-button type="text" size="small" @click="delSingleGoods(orderScope.row)">
                    删除
                  </el-button>
                  <el-button type="text" size="small" @click="addOrderList(orderScope.row)">
                    增加
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="operator_buttom">
              <small>总金额：</small>{{ this.totalMoney }}
            </div>
            <div class="div-btn">
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="delAllGoods()">清空</el-button>
              <el-button type="success" @click="checkOut">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单" name="idleIt">
            挂单
          </el-tab-pane>
          <el-tab-pane label="外卖" name="outsaleIt">
            外卖
          </el-tab-pane>
        </el-tabs>

      </el-col>

      <el-col span="17">
        <div class="hots">
          <div class="hots_title">热销</div>
          <div class="hots_list">
            <ul>
              <li v-for="items in oftenGoods" @click="addOrderList(items)" :key="items.goodsId">
                <!--
                <el-button @click="addOrderList(items)" :key="items.goodsId">
                  {{ items.goodsName }}
                </el-button>
                -->
                <span class="foodImg"><img :src="items.goodsImg"></span>
                <span>{{ items.goodsName }}</span>
                <span class="single_price">￥{{items.price}}</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="goods_type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div>
                <ul class='cookList'>
                  <li v-for="goods in type0Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="single_price">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <div>
                <ul class='cookList'>
                  <li v-for="goods in type1Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="single_price">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div>
                <ul class='cookList'>
                  <li v-for="goods in type2Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="single_price">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div>
                <ul class='cookList'>
                  <li v-for="goods in type3Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                    <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="single_price">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
'use strict';
import axios from 'axios';
import Mock from 'mockjs';

Mock.mock(
  'http://jspang.com/DemoApi/oftenGoods.php',
  {
    "foodInfo|4":[{
      "goodsImg": Mock.Random.image('50x50'),
      "goodsId|+1": 1,
      "goodsName": "@cname",
      "price|10-30": 20
    }]
  }
)
// Another Url: http://jspang.com/DemoApi/typeGoods.php
/*
Mock.mock(
  'http://jspang.com/DemoApi/typeGoods.php',
  {
    "foodInfo|4":[{
      "goodsId|+1": 1,
      "goodsImg": Mock.Random.image('50x50'),
      "goodsName": "@cname",
      "price|10-30": 20 
    }]
  }
)
*/
export default {
  name: 'pos',
  data(){
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalCount: 0,
      totalMoney: 0,
    }
  },
  created:function(){

    // http://jspang.com/DemoApi/oftenGoods.php
    
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
    .then(response=>{
      // console.log(response);
      this.oftenGoods = response.data.foodInfo;
      // console.log(this.oftenGoods[1].goodsId);
    })
    .catch(error=>{
      console.log(error);
      alert("Fuck network!\n");
    })
    // http://jspang.com/DemoApi/oftenGoods.php
    // http://jspang.com/DemoApi/typeGoods.php
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
    .then(response=>{
      // console.log(response);
      this.type0Goods = response.data.foodInfo;
      this.type1Goods = response.data.foodInfo;
      this.type2Goods = response.data.foodInfo;
      this.type3Goods = response.data.foodInfo;

      /*
      this.type0Goods = response.data.foodInfo[0];
      this.type1Goods = response.data.foodInfo[1];
      this.type2Goods = response.data.foodInfo[2];
      this.type3Goods = response.data.foodInfo[3];
      */
      // console.log(this.type0Goods);
    })
    .catch(error=>{
      alert("Fucking Network!!!!\n");
    })
  },
  mounted:function(){
    var orderHeight = document.body.clientHeight;
    // console.log(orderHeight);
    document.getElementById('order-list').style.height = orderHeight + 'px'
  },
  methods: {
    addOrderList(goods){
      // Add something in menu
      // to check if it exists
      let isHave = false;
      let currentId = 0;
      let currentName;
      for(let i = 0; i < this.tableData.length; i++){
        if(this.tableData[i].goodsId == goods.goodsId){
          isHave = true;
          currentId = i
        }
      }
      // coding according the logic parameters
      if(isHave){
        // Add count
        
        let arr = this.tableData.filter(function(x){
            if(x.goodsId == goods.goodsId)
              return x;
        });
        currentName = arr[0].goodsName;
        arr[0].count += 1;
        
        // In single line statement:
        /*
        let arr = this.tableData.filter(o=>o.goodsId == goods.goodsId);
        console.log(arr[0].count);
        arr[0].count += 1;
        */

      }
      else{
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        // currentId = goods.goodsId;
        currentName = newGoods.goodsName;
        this.tableData.push(newGoods);
        // console.log("new added");
      }
      
      // count sum values
      // debugger;
      /*
      this.tableData.forEach(
        (element)=>{
          this.totalMoney += this.tableData[currentId].price;
          this.totalCount += element.count;
          // this.totalMoney = this.totalMoney + (element.price * element.count);
        }
      );
      */
      {
        let tempFood = this.tableData.filter(o=>o.goodsName == currentName);
        let currentPrice = tempFood[0].price;
        this.totalMoney += currentPrice;
      }
    },
    delSingleGoods(goods){
      let tempFood = this.tableData.filter(o=>o.goodsName == goods.goodsName);
      let currentCount = tempFood[0].count;
      let currentPrice = tempFood[0].price;
      this.totalMoney -= currentCount * currentPrice;
      this.totalCount -= currentCount;
      this.tableData = this.tableData.filter(o=>o.goodsId != goods.goodsId);
    },
    delAllGoods(){
      this.tableData = [];
      this.totalMoney = 0;
      this.totalCount = 0;
    },
    checkOut(){
      if(this.totalCount == 0 || this.tableData.length == 0){
        this.$message.error('空购物车无法下单！')
      }
      else
      {
        this.$message({
          message: '下单成功',
          type: 'success'
        });
      }
    }
  },
}
</script>

<style scoped>
*{
  font-family: "Microsoft YaHei";
}
.pos-order{
  background-color: #F9FAFC;
  border-right: 1px solid #C0CCDA;
}
.tri-navigator{
  margin-left: 10px;
}
#order-list>el-tabs>el-tab-pane{
  background-color: red;
  color:red;
  font-size: 24px;
}
.operator_buttom{
  margin-top: 10px;
  border: 1px solid #000;
  background-color: #fff;
  font-size: 16px;
  font-family: "Microsoft YaHei";
}
.div-btn{
  margin-top: 10px;
}
.hots_title{
  height: 20px;
  border-bottom: 1px solid #000;
  background-color: #F9FAFC;
  padding: 10px;
  text-align: left;
}
.hots_list ul li{
  list-style: none;
  height: 50px;
  width: 200px;
  float: left;
  border: 1px solid #E5E9F2;
  padding: 2px;
  margin: 2px;
  background-color: #fff;
  cursor: pointer;
}
.hots_list ul li span{
  float: left;
  line-height: 50px;
}
.single_price{
  color: #58B7FF;
}
.goods_type{
  clear:both;
  margin-left: 10px;
}
.cookList li{
  list-style: none;
  width: 200px;
  height: 50px;
  border: 1px solid #E5E9F2;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span{
  display: block;
  float: left;
  line-height: 50px;
}
.foodImg{
  width: 50px;
}
/*
.foodName{
  font-size: 18px;
  padding-left: 10px;
  color:brown;
}
*/
.foodPrice{
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>



